import * as React from "react"
import PropTypes from "prop-types"
import { Link, useStaticQuery, graphql } from "gatsby"


import "./menu.sass"

const Menu = ({ siteTitle }) => {

  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark(
        filter: {frontmatter: {type: {eq: "Menu"}}}
        sort: { fields: [frontmatter___date], order: DESC}
      ) {
        nodes {
          id
          frontmatter {
            jeux
            categorie
          }
        }
      }
    }
  `)

  const slugify = str =>
    str
      .toLowerCase()
      .trim()
      .replace(/[\s_-]+/g, '-')

  const menu = data.allMarkdownRemark.nodes.map( section => {

    const submenu = section.frontmatter.categorie.map( 
      categorie => <li>
        <Link to={`/jeux/${slugify(section.frontmatter.jeux)}/${slugify(categorie)}`}>{categorie}</Link>
      </li>
      )

    

    return <li class="has-sub">
      <label for={section.id}>{section.frontmatter.jeux}</label><input id={section.id} name="menu" type="radio" />
      <ul class="sub">
        {submenu}
      </ul>
    </li>
  })



  return( 
    <ul class="accordion">
      {menu}
    </ul>
  )
}

/*
Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}
*/
export default Menu
